import "./DocSearch.scss";

import React, {Component} from 'react';

import config from '../../../../gatsby-config.js';

class DocSearch extends Component {

  initialize(props) {
    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;
    let {
      isCollection, isTagged, pathKey, currentVersion
    } = props;
    let algoliaOptions = {
      'facetFilters': []
    }

    if (this.algoliaInitialized) {
      return
    }

    if (isCollection) {
      if (pathKey === '/') {
        algoliaOptions.facetFilters.push("tags:root");
      } else {
        algoliaOptions.facetFilters.push("path:"+pathKey);
        algoliaOptions.facetFilters.push("version:"+currentVersion);
      }
    } else if (isTagged) {
      algoliaOptions.facetFilters.push("version:"+currentVersion);
    }

    window.docsearch({
      appId: ALGOLIA_APP_ID,
      apiKey: ALGOLIA_SEARCH_KEY,
      indexName: ALGOLIA_INDEX_NAME,
      inputSelector: '#algolia-doc-search',
      debug:process.env.NODE_ENV === 'development',
      algoliaOptions: algoliaOptions
    });

    this.algoliaInitialized = true;
  }

  componentWillUnmount() {

  }

  loadAlgoliaJS(props) {
    this.initialize(props);
  }

  render () {

    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;

    return (
      ALGOLIA_APP_ID && ALGOLIA_SEARCH_KEY && ALGOLIA_INDEX_NAME
        ?
          <form className="DocSearch"
            id="DocSearch"
            onMouseOver={() => this.loadAlgoliaJS(this.props)}
            onClick={() => this.loadAlgoliaJS(this.props)}
            onSubmit={e => e.preventDefault()}
            >
            <label>
              <input
                id="algolia-doc-search"
                type="text"
                placeholder="Search"
                aria-label="Search"
                required
                />
              <button className="close-key" type="reset"></button>
            </label>
          </form>
        :
        null
    )
  }

}

export default DocSearch;
